.site{
  &-project{
    &-item{
      background: $white;
      border-radius: 20px;
      margin-bottom: 100px;
      display: flex;
      // align-items: center;
      padding: 40px 40px 0;
      transition: all .3s ease;
      &:hover{
        transform: scale(1.002);
        box-shadow: 0 22px 34px 0 rgba(129,143,151,0.13);
        .site-project-item-content{
          .btn{
            border-color: $text-color;
          }
        }
      }
      &-content{
        width: 60%;
        padding-right: 60px;
        margin-top: 10px;
        span{
          font-size: 16px;
          display: block;
          font-weight: 300;
        }
        h3{
          font-size: 30px;
          color: $text-color;
          margin-bottom: 20px;
          font-weight: 300;
        }
        p{
          font-weight: 300;
          font-size: 16px;
          margin-bottom: 30px;
          color: $text-color-light;
          line-height: 1.8;
        }
      }
      &-image{
        width: 40%;
        img{
          display: block;
          width: 100%;
          border-radius: 15px 15px 0 0;
        }
      }
      &:nth-child(even){
        .site-project-item-content{
          order: 2;
          padding-right: 0;
          padding-left: 60px;
        }
        .site-project-item-image{
          order: 1;
        }
      }
    }
  }
}