.site-navigation{
  padding:10px 30px;
  background: $body-color;
  z-index: 1000;
  .nav-brand{
    padding: 0;
  }
  .nav-item{
    .nav-link{
      font-size: 18px;
      color: $text-color;
      transition: all .3s ease;
    }
    &:not(:last-child){
      .nav-link{
        padding: 15px 30px;
        &:hover{
          color: $primary-color;
        }
      }
    }
    &:last-child{
      margin-left: 20px;
      .nav-link{
        padding: 15px 30px;
        background: $white;
        border-radius: 6px;
        position: relative;
        overflow: hidden;
        z-index: 1;
        &::before{
          position: absolute;
          content: '';
          background: #366EFF;
          width: 0;
          height: 100%;
          left: 0;
          top: 0;
          z-index: -1;
          transition: all .3s ease;
        }
        &:hover{
          color: $white;
          &::before{
            width: 100%;
          }
        }
      }
    }
  }
}